@import url("//fonts.googleapis.com/css?family=Anonymous+Pro");

.home {
    width: 50%;
    height: auto;
    display: flex;
    margin:auto;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    font-family:"Anonymous Pro", sans-serif;
    /*background-position: center;
    background-repeat: no-repeat;
    background-size: cover; */
  }

  
  .characterContainer{
    display: flex;
    flex-direction: row;
  }

  .homeChar{
    width: 30%;
    object-fit: contain;
    height: auto;
  }

  .return-link{
    font-family: "Anonymous Pro",sans-serif;
    font-weight: 700;
    font-style: normal;
    letter-spacing: .1em;
    text-transform: uppercase;
    font-size: .82353em;
  }

  .break{
    clear: both;
    border-top: solid #e8c558;
    height: 0;
    border-width: 3px 0 0;
    margin: 30px 0;
  }
  
