  @media only screen and (max-width: 650px) {
    .home {
      justify-content: center;
      align-items: center;
    }

    .characterContainer{
      display: flex;
      flex-direction: row;
    }

    .homeChar{
      width: 25%;
      object-fit: contain;
      flex: 1;
      height: auto;
    }

    .return-link{
      font-family: "Anonymous Pro",sans-serif;
      font-weight: 700;
      font-style: normal;
      letter-spacing: .1em;
      text-transform: uppercase;
      font-size: .82353em;
    }

    .break{
      clear: both;
      border-top: solid #e8c558;
      height: 0;
      border-width: 3px 0 0 0;
      margin: 30px auto;
      width: 50px;
    }

    
    
  
